import React from "react";
import { Form, Input, Button, message} from 'antd';
import styled from 'styled-components'
import { useStores} from '../stores'
import { useHistory } from "react-router-dom";


const Container = styled.div`
max-width:600px;
box-shadow:2px 2px 4px 0 rgba(0, 0, 0, 0.2);
border-radius:8px;
padding:20px;
margin:30px auto
`
const H1 = styled.h1`
text-align:center;

`


const Component = () => {
   const {AuthStore} = useStores();
   const history = useHistory()


    const onFinish = values => {
        console.log('Success:', values);
        AuthStore.setUsername(values.Username);  //首字母大写
        AuthStore.setPassword(values.password);
        AuthStore.register()
        .then(() => {
            console.log('注册成功')
            message.success('注册成功')
            history.push('/')
        }).catch(() => {
            console.log('注册失败')
            message.warning('注册失败');
        });
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };
  
   const validateUsername = (rule,value) => {  //rule表示自带的规则
       if(/\W/.test(value))  return Promise.reject('只能是字母数字下划线');
       if(value.length < 4 || value.length > 10) return Promise.reject('长度为4~10个字符')
     return  Promise.resolve();
   }



  const validatorConfire = ({getFieldValue}) => ({
  validator(rule,value) {
  if( getFieldValue('password') === value)    return Promise.resolve();  //确认密码规则
  return Promise.reject('两次密码不匹配');

  }

  })

    return (
    <Container>
    <H1>注册</H1>

        <Form
            name="basic"
            labelCol={{ 
                span: 5,
            }}
            wrapperCol={{
                span: 16,    //输入框占据的格数
            }}
            // initialValues={{
            //     remember: true,
            // }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
        >
            <Form.Item
                label="用户名"
                name="Username"
                rules={[
                    {
                        required: true,  //表示必须输入，否者就提示下面的msg
                        message: '请输入你的用户名!',
                    },
                    {
                        validator:validateUsername   //验证用户名，自己加规则
                    }
                ]}
            >
                <Input />
            </Form.Item>

            <Form.Item
                label="密码"
                name="password"
                rules={[
                    {
                        required: true,
                        message: '请输入你的密码!',
                    },
                    {
                      min:4,
                     message:'最少4个字符'   //添加规则
                    },
                    {
                       max:10,
                       message:'最多10个字符' 
                    }

                ]}
            >
                <Input.Password />
            </Form.Item>

            <Form.Item
                label="确认密码"
                name="Confirmpassword"   //改为ConfirePassword确认密码
                rules={[
                    {
                        required: true,
                        message: '请在次确认密码!',
                    },
                
                       validatorConfire  //密码是否和上一次匹配规则
                    
                ]}
            >
                <Input.Password />
            </Form.Item>

            {/* <Form.Item
                name="remember"
                valuePropName="checked"
                wrapperCol={{
                    offset: 8,
                    span: 16,
                }}
            >
                <Checkbox>Remember me</Checkbox>
            </Form.Item> */}

            <Form.Item
                wrapperCol={{
                    offset: 11,
                    span: 16,
                }}
            >
                <Button type="primary" htmlType="submit">提交</Button>
            </Form.Item>
        </Form>

        </Container>
    );
};

export default Component;